<script>
    import Connect from "../Settings/Connect.svelte";
    import {image_mode} from "./settings_stores.js";
    


    function image_mode_change() {
        if ($image_mode === "sim"){
                fetch("./image_mode_change/"+"sim"  )
                    .then(d => d.text());
        }
        else if ($image_mode === "prod"){
            fetch("./image_mode_change/"+"prod"  )
                    .then(d => d.text());
        }

    }
</script>
<div class = "border-top border-bottom pt-8 pb-8">
<div class= "offset-md-2 row align-items-center">
<div class="col-md-2 text-center"><h5>Create Mode:</h5></div>
<div class="col-md-1 text-center">
    <label>
        <input bind:group={$image_mode} on:change={() => image_mode_change} type="radio" value="sim" /> <h6>Simulation</h6>
    </label>
</div>
<div class="col-md-1 text-center">
    <label>
        <input bind:group={$image_mode} on:change={() => image_mode_change} type="radio"  value="prod" /> <h6>Production</h6>
    </label>
</div>
</div>
{#if $image_mode === "prod"}
<div class="offset-md-2 pt-4 pb-4 text-center">
<Connect/>
</div>
{/if}
</div>




