<script>
    import {batch_notes} from './stores.js';
    import {Label } from 'sveltestrap';
    import {Dialog,Button} from "smelte";
    import marked from 'marked'
    let showDialog = false;
    import { batch_number} from './stores.js';
    import { onMount } from 'svelte';
    let b_notes = "";
    function add_batch_notes() {
            if( $batch_notes === ""){
                fetch("./add_batch_notes/" + "nothing_to_see_here")
            .then(d => d.text());  
            }
            else {
               
                b_notes= $batch_notes.replace(/\n/g," ")
                console.log("batch_notes", b_notes)

   
                fetch("./add_batch_notes/" + b_notes)
            .then(d => d.text());  
            }   
        }

    
    let batch_store = {"batch_notes":""};
    onMount(async () => {
            await fetch("./retrieve_batch_notes")
            .then(d => d.text())
            .then(d => JSON.parse(d))
            .then(d => (batch_store = d)
        );
        $batch_notes = batch_store.batch_notes;
        // console.log($batch_notes);
        })
    
    
</script>

        
<div class=" py-2 text-center">
    <div   on:click={() => showDialog = true}>
        <h5 >Batch Notes (Batch No.{$batch_number})</h5>
    </div>

    <div  class="text-break"   on:click={() => showDialog = true}> 
        {#if $batch_notes === ""}
            <h7 >Type batch specific notes here</h7>
      
        {:else}
            <h7 >{@html marked($batch_notes)}</h7>
        {/if}
       
    
    </div>
</div>
<Dialog  bind:value={showDialog}>
    <Label><h3 >Batch Notes (Batch No.{$batch_number})</h3></Label>
    <div class="text-gray-700"><textarea placeholder="Type batch specific notes here"  bind:value={$batch_notes} rows="14" cols="80 " on:change={add_batch_notes}></textarea></div>
    <div slot="actions">
        <Button color="blue" text on:click={() => showDialog = false}>Cancel</Button>
        <Button color="blue" text on:click={() => showDialog = false}>Save</Button>

        
    </div>
</Dialog>

    
    
