<script>
    import Connect from "../Settings/Connect.svelte";
    import {storage_folder} from "./settings_stores.js";
    import { onMount } from "svelte";
    import { TextField, Button } from "smelte";
    import {Badge} from 'sveltestrap'

    let storage_folder_text;
    let storage_folder_save_str;
    let change_outcome_txt;
    $: change_outcome_txt = "";
    // function setAttributeWebkitdirectory(node) {
    // node.setAttribute('webkitdirectory', '');
// }
    
    async function change_storage_folder() {
        if ($storage_folder.charAt(0)=== "/"){
            storage_folder_save_str = $storage_folder.slice(1)
            // console.log("storage_folder_save_str changed", storage_folder_save_str)
        }
        else{
        storage_folder_save_str = $storage_folder
        // console.log("storage_folder_save_str not changed", storage_folder_save_str)
        }

        const response = await fetch("./save_dropbox_path/" + storage_folder_save_str)
        const change_outcome = await response.json();
        
       
        // console.log(change_outcome);
        
        change_outcome_txt = change_outcome
    }   
    
    onMount(async () => {
        await fetch("./get_dropbox_path")
        .then(d => d.text())
        .then(d => JSON.parse(d))
        .then(d => (storage_folder_text = d));
        // console.log(storage_folder_text)
        $storage_folder = storage_folder_text;
        
    })

</script>

<div class = "border-bottom pt-8 pb-12">
    <div class= "offset-md-2 row align-items-center">   
        <div class="col-md-2 text-center"><h5>Storage:</h5></div>
        <div class=" col-md-4 ">
            <TextField bind:value={$storage_folder} color="blue" placeholder="Copy Dropbox Database folder path here" label="Dropbox Database" outlined on:change={change_storage_folder}/>
        </div>
    </div>
    <div class= "offset-md-2 row align-items-center"> 
    <div class="offset-md-2 col-md-4 "><h7><Badge class= "align-top" color = "info">{change_outcome_txt}</Badge></h7></div>
    </div>

</div>




