                    <!-- svelte-ignore a11y-no-onchange -->
                    <select bind:value={$batch_number} on:change={send_batch_number} >
                        {#each $batch_collection as batch_collection_number}
                        <option  value={batch_collection_number} >{batch_collection_number}</option>
                        {/each}
                    <select> 
                </div>
                {/if}
        {/if}


        {#if $is_clicked_begin_collection }
            {#if $fetching_images}
            <div class="col-md-3">
                <Button  disabled  >Fetching Images From Machine </Button>
            </div>

            {:else  if !$fetching_images}
                {#if !$is_Clicked_start}
                    <div class="col-md-3">
                        <Button  color = "blue" on:click={send_start_batch} >Start Batch {$batch_number}</Button>
                    </div>
                
                {:else  if $is_Clicked_start }
                    <div class="col-md-3">
                        <Button disabled   >Start Batch {$batch_number}</Button>
                    </div>
                {/if}
            {/if}
            
        {/if}
        {#if !$is_clicked_begin_collection }
            {#if !$is_Clicked_start}
                <div class="col-md-3">
                    <Button  color = "blue" on:click={send_start_batch} >Start Batch {$batch_number}</Button>
                </div>
            {/if}
            {#if $is_Clicked_start }
                <div class="col-md-3">
                    <Button color = "alert"  >Start Batch {$batch_number}</Button>
                </div>
            {/if}
        {/if}
        
        

        
        {#if $is_Clicked_end }
            {#if $fetching_images}
                <div class="col-md-3">
                    <Button  disabled  >Fetching Images From Machine </Button>
                </div>
            {:else  if !$fetching_images}
                {#if $button_alert_change }
                <div class="col-md-3">
                    <Button  color = "alert" on:click={send_end_batch} >End Batch </Button>
                </div>
                {/if}
                {#if !$button_alert_change }
                <div class="col-md-3">
                    <Button  color = "blue" on:click={send_end_batch} >End Batch </Button>
                </div>
                {/if}
            {/if}

        {/if}

        {#if !$is_Clicked_end}
            {#if $fetching_images}
                <div class="col-md-3">
                    <Button  disabled  >Fetching Images From Machine </Button>
                </div>
            {:else  if !$fetching_images}
                <div class="col-md-3">
                    <Button disabled color = "blue"  >End Batch </Button>
                </div>
            {/if}
        {/if}

        

        {#if $button_alert_change }
            {#if !$is_Clicked_cancel }
                <div class="col-md-2">
                    <Button  color = "error" on:click={cancel_batch} >Cancel Batch </Button>
                </div>
            {/if}

        

            {#if $is_Clicked_cancel}
                <div class="col-md-2">
                    <Button disabled color = "error"  >Cancel Batch </Button>
                </div>
            {/if}
        
        {/if}
            
        
        
{/if}  
{#if !$ready_to_work}  

<div class="col-md-2">
    <!-- svelte-ignore a11y-no-onchange -->
    <select bind:value={$batch_number} on:change={send_batch_number} disabled >
        {#each $batch_collection as batch_collection_number}
        <option  value={batch_collection_number} >{batch_collection_number}</option>
        {/each}
    <select> 
</div>

        <div class="col-md-3">
            <Button disabled  color = "blue" >Busy Processing Images</Button>
        </div>
  



        <div class="col-md-3">
            <Button disabled color = "blue" >Busy Processing Images</Button>
        </div>



{/if}  


</div>
<div class="col-md-3 offset-md-6 " rows="4" cols="4 ">
<Dialog  bind:value={showDialog_Cancel_Batch}>

        <h4 >Are you sure?</h4>
        <div>   
            <h7 > Doing so will result in the <u>loss</u> of any images in the current batch</h7> 
        </div>
        
    
    <div slot="actions">
        <Button color="blue" text on:click={() => dialog_cancel_batch("Yes")}>Yes</Button>
        <Button color="blue" text on:click={() => dialog_cancel_batch("No")}>No</Button>
        
    </div>
    
</Dialog>
</div>