<script>
    import { Button, Chip, Dialog, TextField, Tabs, Tab} from "smelte";
    import { onMount } from "svelte";
    import { Fade, FormGroup, Label } from 'sveltestrap';
    import {is_company_selected, is_product_selected, is_user_selected,
         collection_search_term, image_collection_names,
         matching_company_names, matching_product_names,
         matching_user_names, company_selection, product_selection, 
         user_selection,  is_clicked_begin_collection, is_clicked_end_collection,
        collection_button_alert, new_collection_name, next_step, collection_id, 
        is_Clicked_start, ready_to_end_collection, send_end_collection_delete_batches, 
        fetching_images, collection_running, completed_collection,matching_local_collection_ids,
        company_search_term, product_search_term, user_search_term, 
        matching_local_product_names, matching_local_company_names, 
        matching_local_user_names, local_collection_id, collection_ids, ending_collection} from './stores.js';

    let showDialog = false;
    let alert_collection_running = false;
    let tab_selected = "1";


    onMount(async() => {
        const response = await fetch("/retrieve_selections")
        console.log("retrieved_selections_response", response)
        const retrieved_selections = await response.json();
        console.log("retrieved_selections", retrieved_selections)
        $company_selection = retrieved_selections.company_selection;
        $product_selection = retrieved_selections.product_selection;
        $user_selection = retrieved_selections.user_selection;
        $collection_id = retrieved_selections.collection_id
        // console.log($company_selection,$product_selection,$user_selection)


    })
    function handleClick(type) {
        if (!$is_clicked_begin_collection){
            if (type === "company"){
                tab_selected = "1";
                
                
            }
            if (type === "product"){
                tab_selected = "2";
                // console.log("product selected");
            }
            if (type === "user"){
                tab_selected = "3";
                // console.log("user selected");
            }
            showDialog = true;
        }
        if ($is_clicked_begin_collection){
            alert_collection_running = true;
            // console.log("alert_collection_running = true")
            fetch("./send_next_step/"+ "In order to change collection details - End Current Collection First")
            .then(d => d.text());
            
        }
    }
    
    
    
    async function add_to_collection(collection_type) {
            const response = await fetch("./add_to_collection/"+ 
            collection_type + "/" +  $new_collection_name);
            const add_to_collection_response = await response.json();
            console.log("add_to_collection_finished")
            if (collection_type === "company"){ 
            
                $matching_company_names = add_to_collection_response
            
            change_content_selection(collection_type);
            $is_company_selected = true;
            
            }
            if (collection_type === "product"){
                $matching_product_names = add_to_collection_response
                change_content_selection(collection_type);
                const response = await fetch("./ready_for_id");
                const collection_id_response = await response.json();
                $collection_id  = collection_id_response
                // console.log("$collection_id", $collection_id)
                $is_product_selected = true;
            }
            if (collection_type === "user"){
                $matching_user_names = add_to_collection_response
                change_content_selection(collection_type);

                $is_user_selected = true;
            }
        }
    
    function change_content_selection(collection_type){
        if (collection_type === "company"){
            $company_selection = $new_collection_name;
        }
        if (collection_type === "product"){
            $product_selection = $new_collection_name;
        }
        if (collection_type === "user"){
            $user_selection = $new_collection_name;
        }

        $collection_search_term = "";
    
    }



    

    async function send_company_selection(change_type)  {
        if ($company_selection){
            fetch("./send_company_selection/"+ $company_selection)
            .then(d => d.text());
            // console.log($company_selection);
            $is_company_selected = true;
            // console.log($company_selection);
    }
    
    }

    async function send_product_selection()  {
        if ($product_selection){
            fetch("./send_product_selection/"+ $product_selection)
            .then(d => d.text());
            // console.log($product_selection);
            $is_product_selected = true;
            // fetch("./ready_for_id").then(d => d.text());
            const response = await fetch("./ready_for_id");
            const collection_id_response = await response.json();
            // console.log("collection_id_response1", collection_id_response);
            $collection_id  = collection_id_response
            // console.log("$collection_id 1", $collection_id)
        }
    }

    function send_user_selection()  {
        if ($user_selection){
            fetch("./send_user_selection/"+ $user_selection)
            .then(d => d.text());
            // console.log($user_selection);
            $is_user_selected = true;
    
        }
    }
    

    function send_collection_search_term(collection_type) {
        // console.log($collection_search_term);
        if (collection_type === "company"){
            $collection_search_term  = $company_search_term;
            $is_company_selected = true;
            // console.log($collection_search_term);
        }

        else if (collection_type === "product") {
            $collection_search_term  = $product_search_term;
            $is_product_selected = true;
            // console.log($collection_search_term);
        }

        else if (collection_type === "user") {
            $collection_search_term  = $user_search_term;
            $is_user_selected = true;
            // console.log($collection_search_term);
        }

        if (!$collection_search_term)
        {
            // console.log($collection_search_term);
            $collection_search_term = "get_all";
        

            // console.log($collection_search_term);
            fetch("./send_collection_search_term/"+  collection_type + "/" + $collection_search_term)
                .then(d => d.text());
                // console.log($collection_search_term);
                
            
            $collection_search_term = "";
            // console.log($collection_search_term);
            
        }
        else{
            fetch("./send_collection_search_term/"+ collection_type + "/" + $collection_search_term)
            .then(d => d.text());
            // console.log($collection_search_term);


        }

    }
    
    async function send_begin_collection() {
        // console.log("beginning collection");
        // console.log($company_selection);
        // console.log($product_selection);
        // console.log($user_selection);
        // console.log($collection_id);
        if (!($company_selection === "" || $product_selection === "" || $user_selection === "" || $collection_id === "") ){
            $collection_running = true
            // console.log("in the beginning of  collection");
            $is_clicked_begin_collection = true;
            $collection_button_alert = false;
            fetch("./is_clicked_begin_collection/" + $is_clicked_begin_collection).then(d => d.text());
            // console.log("began collection")
            
            fetch("./is_Clicked_start/" + false)
            .then(d => d.text());
            // console.log("unclicked start")
            $is_Clicked_start = false;
            fetch("./is_clicked_end_collection/" + false).then(d => d.text());
            $is_clicked_end_collection = false;
            const response = await fetch("./send_begin_collection/"+ $company_selection + "/"+ $product_selection + "/" +
            $user_selection+ "/"+ $collection_id);
            const collection_id_response = await response.json();
            $collection_id  = collection_id_response
           
            
            
        }
    }
    async function send_end_collection() {
        fetch("./is_clicked_begin_collection/" + false).then(d => d.text());
        fetch("./is_clicked_end_collection/" + true).then(d => d.text());
        const send_end_collection_response = await fetch("./send_end_collection");
        const send_end_collection_response_string = await send_end_collection_response.json();
        console.log("send_end_collection_response_string", send_end_collection_response_string)
        if (send_end_collection_response_string == "ended collection"){ 
            $local_collection_id = $collection_id
            console.log("($local_collection_id collection run", $local_collection_id)
            const response = await fetch("./retrieve_completed_collection/" + $local_collection_id);
            const retrieve_completed_collection_response = await response.json();
            console.log("retrieve_completed_collection_response.completed_collection",retrieve_completed_collection_response.completed_collection)
            $completed_collection = retrieve_completed_collection_response.completed_collection
            // console.log("$local_collection_id",retrieve_completed_collection_response.collection_id)

            

        }
        


        alert_collection_running = false;
        $is_clicked_begin_collection = false;
        $is_clicked_end_collection = true;
        
        const response_one = await fetch("./retrieve_local_details")
        const retrieve_local_details_response = await response_one.json();
        $matching_local_company_names = retrieve_local_details_response.local_companies
        $matching_local_product_names = retrieve_local_details_response.local_products
        $matching_local_user_names = retrieve_local_details_response.local_users
        $matching_local_collection_ids = retrieve_local_details_response.collection_ids
        $collection_ids = retrieve_local_details_response.collection_ids
        // console.log("ended collection")
        // console.log("ready_to_end_collection", $ready_to_end_collection)
        
        // send_local_collection_id($collection_id)
        // console.log("col_id", col_id)
        // $local_collection_id = $collection_id
        // const send_collection_id_response = await fetch("./send_collection_id/"+ $collection_id)
        // const completed_collection_response = await send_collection_id_response.json();
        // $completed_collection = completed_collection_response.completed_collection
        // console.log("$completed_collection",$completed_collection)
    }
    
   
    let loading = false;

    function enable_collection_button_alert() {
        $collection_button_alert = true;
        fetch("./send_next_step/"+ "Select all Collection Details Before Beginning a Collection")
        .then(d => d.text());
        // console.log($next_step)
        }

  
   

    
</script>




<!-- svelte-ignore non-top-level-reactive-declaration -->
<Dialog  bind:value={showDialog}  >
    
    
    
    
    <div style="width: 1300px" >
        <Tabs
            
            selected={tab_selected}
            class="bg-grey-300 shadow-sm mt-6 text-grey-500"
            color="blue"
            notSelectedColor = "light"
            let:selected
            {loading}
            items={[
            { id: '1', text: 'Company', icon: 'business' },
            { id: '2', text: 'Product', icon: 'restaurant' },
            { id: '3', text: 'User', icon: 'face' }
            ]}>
            <div
                slot="content"
                class="flex items-stretch md:items-center content-center overflow-hidden w-full bg-white shadow h-full"
                style="height: 250px">
                <Tab id="1" {selected}  >
                    <div on:mousemove={() => send_company_selection("movemouse")} class="col-md-3 offset-md-2 " > 
                        <TextField label = "Search Company" color="blue" bind:value={$company_search_term} on:input={() => send_collection_search_term("company")} outlined />
                            
                        
                            Select Company:
                            <!--  svelte-ignore a11y-no-onchange --> 
                            <select  open bind:value={$company_selection} on:change={() =>  send_company_selection("change")}  >
                                <!-- <option value="" selected disabled hidden>Choose here</option>  -->
                                {#each $matching_company_names as collection_name}
                                    <option  value={collection_name} >{collection_name}</option>
                                {/each}
                            <select>
                    </div>
                    <div class=" col-md-1 offset-md-1 text-center">
                    <h4>Or</h4>
                    </div> 
                    <div  class="col-md-3 offset-md-1 ">   
                        
                        
                        <FormGroup>
                            <TextField label = "Type New Company Name" maxlength="50" color="blue" bind:value={$new_collection_name} outlined />
                            <Label ><Button outlined on:click={() => add_to_collection("company")} color="blue">Create New Company </Button></Label>
                        </FormGroup>
            
                    </div> 
                </Tab>
                <Tab id="2" {selected}  >
                    <div on:mousemove={send_product_selection} class="col-md-3 offset-md-2 "> 
                        <TextField label = "Search Product" color="blue" bind:value={$product_search_term} on:input={() => send_collection_search_term("product")} outlined />
                            
                        
                            Select Product:
                            <!--  svelte-ignore a11y-no-onchange --> 
                            <select open bind:value={$product_selection} on:change={send_product_selection}   >
                                <!-- <option value="" selected disabled hidden>Choose here</option> -->
                                {#each $matching_product_names as collection_name}
                                    <option  value={collection_name}  >{collection_name}</option>
                                {/each}
                            <select>
                    </div> 

                    <div class="col-md-1 offset-md-1 text-center">
                        <h4>Or</h4>
                    </div> 
                    <div class="col-md-3 offset-md-1 ">   
                        
                        
                        <FormGroup>
                            <TextField label = "Type New Product Name" maxlength="50" color="blue" bind:value={$new_collection_name} outlined />
                            <Label ><Button outlined on:click={() => add_to_collection("product")} color="blue">Create New Product </Button></Label>
                        </FormGroup>
            
                    </div> 
                </Tab>
                <Tab id="3" {selected} >
                    <div on:mousemove={send_user_selection} class="col-md-3 offset-md-2 "> 
                        <TextField label = "Search User" color="blue" bind:value={$user_search_term} on:input={() => send_collection_search_term("user")} outlined />
                            
                        
                            Select User:
                            <!--  svelte-ignore a11y-no-onchange --> 
                            <select  selected open bind:value={$user_selection} on:change={send_user_selection}  >
                                <!-- <option value="" selected disabled hidden>Choose here</option> -->
                                {#each $matching_user_names as collection_name}
                                    <option  value={collection_name} >{collection_name}</option>
                                {/each}
                            <select>
                    </div> 

                    <div class="col-md-1 offset-md-1 text-center">
                        <h4>Or</h4>
                    </div> 
                    <div class="col-md-3 offset-md-1 ">   
                        
                        
                        <FormGroup>
                            <TextField label = "Type New User Name" maxlength="50" color="blue" bind:value={$new_collection_name} outlined />
                            <Label ><Button outlined on:click={() => add_to_collection("user")} color="blue">Create New User </Button></Label>
                        </FormGroup>
            
                    </div> 
                </Tab>
            </div>
        </Tabs>
        
    </div>
    <div class="flex justify-center" slot="actions">
        <Button color="blue" text on:click={() => showDialog = false}>Done</Button>

        
    </div>
  
</Dialog>

<div class="border-bottom pb-4"> 
    <!-- <div class="row align-items-center"> -->
    <div class="  h-48 flex flex-wrap content-evenly">
        <div class=" break-all  col-md-2  grid justify-items-center place-content-between gap-8">   
                {#if $is_company_selected}
                    {#if alert_collection_running}
                        <Button
                                
                        icon="business"
                        color="blue"
                        outlined
                        >Company</Button>
                        <div><h6>{$company_selection}</h6></div>
                    
                    {:else}
                        <Button
                        
                        icon="business"
                        color="blue"
                        outlined
                        on:click={() => handleClick("company")}
                        >Company</Button>
                        <div ><h6>{$company_selection}</h6></div>
                    {/if}

                    
                {:else}
                    <Button
                    icon="business"
                    color="dark"
                    outlined
                    on:click={() => handleClick("company")}
                    >Company</Button>
                {/if}
            
            


        </div>

        <div class="col-md-2 break-all grid justify-items-center place-content-between gap-8">  
            
            
                {#if $is_product_selected}
                
                    {#if alert_collection_running}
                        <Button
                        icon="restaurant"
                        color="blue"
                        outlined
                        >Product</Button>
                        <h6>{$product_selection}</h6>
                    {/if}
                    {#if !alert_collection_running}
                        <Button
                        icon="restaurant"
                        color="blue"
                        outlined
                        on:click={() => handleClick("product")}
                        >Product</Button>
                        <h6>{$product_selection}</h6>
                    {/if}
                    

                {/if}

                {#if !$is_product_selected}
                    <Button
                    icon="restaurant"
                    color="dark"
                    outlined
                    on:click={() => handleClick("product")}
                    > Product</Button>
                {/if}
            
           
        </div>
        
        <div class="col-md-2  break-all grid justify-items-center place-content-between gap-8">   

            {#if $is_user_selected}
                    {#if alert_collection_running}
                        <Button
                        icon="face"
                        color="blue"
                        outlined
                        >User</Button>
                        <h6>{$user_selection}</h6>
                    {/if}
                    {#if !alert_collection_running}
                        <Button
                        icon="face"
                        color="blue"
                        outlined
                        on:click={() => handleClick("user")}
                        >User</Button>
                        <h6>{$user_selection}</h6>
                    {/if}
                
            {/if}

            {#if !$is_user_selected}
                <Button
                icon="face"
                color="dark"
                outlined
                on:click={() => handleClick("user")}>User
                </Button>

               
                
            {/if}
        </div>

        <div class="col-md-2 break-all grid justify-items-center place-content-between gap-8"> 
            {#if $is_product_selected }
                <Button
                icon="vpn_key"
                color="blue"
                outlined
                text >
                ID
                </Button>
                <h6>{$collection_id}</h6>
            {/if}

            
        </div>
    
        <div class="col-md-2 grid justify-items-center place-content-between gap-8"> 
            {#if !$is_clicked_begin_collection}
                {#if $is_product_selected & $is_company_selected & $is_user_selected }
                    <div class="col-md-4">
                        <Button  color = "blue" on:click={send_begin_collection} >Begin Collection</Button>
                    </div>
                
                {:else}
                    {#if $collection_button_alert}
                    <div class="col-md-4">
                        <Button color = "alert"  >Begin Collection</Button>
                    </div>
                    {:else}
                    <div class="col-md-4">
                        <Button  color = "blue" on:click={enable_collection_button_alert} >Begin Collection</Button>
                    </div>

                    {/if}

                {/if}
            {/if}
            {#if $is_clicked_begin_collection || !send_end_collection_delete_batches || $fetching_images || $ending_collection}
                <div class="col-md-4">
                    <Button disabled   >Begin Collection</Button>
                </div>
            {/if}    
            {#if $is_clicked_end_collection || !$ready_to_end_collection || $fetching_images || $ending_collection}
            <div class="col-md-4">
                <Button disabled   >End Collection </Button>
            </div>
            {/if} 
            {#if !$is_clicked_end_collection && $ready_to_end_collection && !$fetching_images && !$ending_collection}
            <div class="col-md-4">
                <Button color = "blue" on:click={send_end_collection} >End Collection </Button>
            </div>
            {/if}
            
    
               
<!--             
            <div class="col-md-4">
                <Button  color = "blue" on:click={end_collection}  >End Collection </Button>
            </div>
             -->
<!--     
            

            {#if !$is_clicked_end_collection }
                {#if $is_product_selected & $is_company_selected & $is_user_selected}
                <div class="col-md-4">
                    <Button  color = "blue" on:click={send_end_batch} >End Collection </Button>
                </div>
                {/if}
                {#if $collection_button_alert }
                <div class="col-md-4">
                    <Button  color = "alert" on:click={send_end_collection} >End Collection </Button>
                </div>
                {/if}
    
            {/if} --> 
        </div>

    </div>

</div>




